

* {
  box-sizing: border-box;
}


body, html{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}

.app-container {
  min-height:100dvh;
  display:flex;
  justify-content:center;
  position: relative;
  width:100vw;
  height:100vh;
}


.page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100vh;
    justify-content:center;
  }

:root{
    --pr-color:#32cd32;
    --sc-color:rgb(80, 128, 96);
}

/* Styles for screens larger than laptops (1024px and up) */
@media (min-width: 1024px) {
  .page-container {
    max-width: 769px;
    border:1px solid black;
  }
}
